<template>
    <section>
        <tg-list :IsShowAction="false" :listType="['List']" ModuleName="Asset" SubModuleCode="Asset"
            IdentityColumn="ASSET_CATALOGUE_ID" HeaderText="New Product Type Request" :ListData="ProductData"
            :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions"
            :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick"
            v-on:SortdataButtonClick="sortdata">

        </tg-list>
        <RelationPopup v-if=IsShowRelation></RelationPopup>
    </section>
</template>

<script>
import Schema from "../AssetRequisition/NewAssetRequisitionListingSchema"
import DataService from '../../service/DataService'
import RelationPopup from '../../components/Modal/AddRelationPopup.vue'
export default {
    components: {
        RelationPopup
    },
    data() {
        return {
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            LegendArray: [],
            ProductData: [],
            PageSize: 10,
            PageNumber: 1,
            PageSizeOption: null,
            StatusListForListing: [],
            Headers: [{
                "COLUMN_NAME": this.$t('Product Name'),
                "DATA_TYPE": "image",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Product Name",
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": this.$t('Product Type'),
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Product Type",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": this.$t('Requested Quantity'),
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Requested Quantity",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": this.$t('Description'),
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Description",
                "DISPLAY_ORDER": 4,
                settings: {
                    clickEvent: this.clickEventOfColumn,
                    formatter: this.columnDataFormatter,
                }


            },
            {
                "COLUMN_NAME": this.$t('Created By'),
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created By",
                "DISPLAY_ORDER": 5,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": this.$t('Created At'),
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created At",
                "DISPLAY_ORDER": 6,
                settings: {
                    isInSlot: true
                }
            },

            {
                "COLUMN_NAME": this.$t('View'),
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "View",
                "DISPLAY_ORDER": 7,
                settings: {
                    isInSlot: true
                }
            }


            ],
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SortBy: '',
            SortExp: '',
            widgets: [

            ],
            legendProgress: [],
            IsShowRelation: false,
        }
    },
    created() {
        var vueObj = this;
        vueObj.FetchData();
        // Schema.bindleftCommonSearchdropdown(vueObj, "Asset", "Location"); 
        // Schema.bindleftCommonSearchdropdown(vueObj, "Asset", "Tag");  
        // Schema.bindleftCommonSearchdropdown(vueObj, "Asset", "Status");           
    },

    methods: {
        columnDataFormatter: function (row, column) {
            return row[column.COLUMN_NAME];

        },        
        FetchData() {
            var vm = this;
            var url = `search=&pageSize=10&pageNum=1&sortBy=&sortExp=&statusIds=&LocationId=&tagIds=`;
            DataService.AssetCatalogueListing(url).then(response => {
                if (response.data != null) {
                    if (response.data.data.length > 0) {

                        vm.ProductData = response.data.data;
                        vm.ProductData.forEach((row) => {
                            row.rowDetailsUrl = '';
                            if (row.rowDetailsUrl == '') {
                                row.rowDetailsUrl = '/AssetCatalog/ManageProducts/' + row.ASSET_CATALOGUE_ID + '';
                            }
                        })
                        vm.TotalRecords = 50;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                    }
                    else {
                        vm.noRecord = false;
                        vm.ProductData = [];
                        vm.TotalRecords = 0
                    }
                }
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata')
                }, 500);
            });
        },




        clickEventOfColumn: function (row, column) {


        },
        leftCommonSearch() {

        },
        currentPageMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        GetPageSizeOption: function () {
            var list = [];
            var defaultPageSize = 10;
            for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
                list.push(i);
            }
            this.PageSizeOption = list;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },

        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {

                case "RequestNewProductType":
                    debugger;
                    this.RequestNewProductType();
                    break;

            }
        },

        RequestNewProductType() {
            debugger;
            this.$router.push({ path: '/AssetRequisition/AddNewRequisition' })

        },


        AddRelationShipPopup() {

        },
        CloseAddRelationShipPopup() {

        },
        actionButtonClick() {

        },
        sortdata: function (s) {

        },

        renderActions(row) {

        },

    },
    updated: function () {
        this.BindActionButtonEvent();
    },
}

</script>